nslate(95.7371vw, 55.6vh) scale(0.041);\n  }\n  to {\n    transform: translate(91.56755vw, 100vh) scale(0.041);\n  }\n}","@charset \"utf-8\";\r\n/* byui scss全局变量开始 */\r\n/*浅色风格开始*/\r\n$base-menu-background: #e7eaed;\r\n$base-menu-active-background: rgba(#1890ff, 0.5);\r\n$base-menu-text: #001529;\r\n$base-menu-text-active: #001529;\r\n$base-title: #001529;\r\n/*浅色风格结束*/\r\n\r\n/*蓝色风格开始*/\r\n$base-menu-background: #2a58ad;\r\n$base-menu-active-background: #22468a;\r\n$base-menu-text: #ffffff;\r\n$base-menu-text-active: #ffffff;\r\n$base-title: #ffffff;\r\n/*蓝色风格结束*/\r\n/*黑色风格开始*/\r\n$base-menu-background: #001529;\r\n$base-menu-active-background: #009688;\r\n$base-menu-text: #ffffff;\r\n$base-menu-text-active: #ffffff;\r\n$base-title: #ffffff;\r\n/*黑色风格结束*/\r\n$base-font-size-small: 12px;\r\n$base-font-size-default: 14px;\r\n$base-font-size-big: 16px;\r\n$base-font-size-bigger: 18px;\r\n$base-font-size-max: 22px;\r\n$base-color-header: $base-menu-background;\r\n$base-color-blue: #009688;\r\n$base-color-green: #67c23a;\r\n$base-color-white: #ffffff;\r\n$base-color-black: #000000;\r\n$base-color-yellow: #ffba00;\r\n$base-color-red: #ff4d4f;\r\n$base-color-gray: #97a8be;\r\n$base-main-width: 1279px;\r\n$base-border-radius: 2px;\r\n$base-border-color: #ebeef5;\r\n$base-form-width: 600px;\r\n$base-input-height: 32px;\r\n$base-pagination-height: 28px;\r\n$base-dialog-title-height: 40px;\r\n$base-padding: 15px;\r\n$base-box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);\r\n$base-font-color: #606266;\r\n$base-left-menu-width: 225px;\r\n$base-right-content-width: calc(100vw - 225px);\r\n$base-left-menu-width-min: 65px;\r\n$base-right-content-width-min: calc(100vw - 65px);\r\n/* byui scss全局变量结束 */\r\n:export {\r\n  menuText: $base-menu-text;\r\n  menuTextAcive: $base-menu-text-active;\r\n  menuBackground: $base-menu-background;\r\n  menuActiveBackground: $base-menu-active-background;\r\n  tagViewsActiveBackground: $base-color-blue;\r\n}\r\n","@import \"~@/styles/variables.scss\";\r\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n.content {\n  background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);\n  overflow: hidden;\n  filter: drop-shadow(0 0 10px white);\n  position: relative;\n  width: 100%;\n  height: 100%;\n}\n\n@function random_range($min, $max) {\n  $rand: random();\n  $random_range: $min + floor($rand * (($max - $min) + 1));\n  @return $random_range;\n}\n\n.snow {\n  $total: 200;\n  position: absolute;\n  width: 10px;\n  height: 10px;\n  background: white;\n  border-radius: 50%;\n\n  @for $i from 1 through $total {\n    $random-x: random(1000000) * 0.0001vw;\n    $random-offset: random_range(-100000, 100000) * 0.0001vw;\n    $random-x-end: $random-x + $random-offset;\n    $random-x-end-yoyo: $random-x + ($random-offset / 2);\n    $random-yoyo-time: random_range(30000, 80000) / 100000;\n    $random-yoyo-y: $random-yoyo-time * 100vh;\n    $random-scale: random(10000) * 0.0001;\n    $fall-duration: random_range(10, 30) * 1s;\n    $fall-delay: random(30) * -1s;\n\n    &:nth-child(#{$i}) {\n      opacity: random(10000) * 0.0001;\n      transform: translate($random-x, -10px) scale($random-scale);\n      animation: fall-#{$i} $fall-duration $fall-delay linear infinite;\n    }\n\n    @keyframes fall-#{$i} {\n      #{percentage($random-yoyo-time)} {\n        transform: translate($random-x-end, $random-yoyo-y) scale($random-scale);\n      }\n\n      to {\n        transform: translate($random-x-end-yoyo, 100vh) scale($random-scale);\n      }\n    }\n  }\n}\n"]}]);
